<template>
  <div>
    <el-button type="primary" @click="downloadImg">html2canvas</el-button>
    <el-button type="primary" @click="loading">loading IE  兼容</el-button>
  </div>
</template>

<script>
import html2canvas from "html2canvas";
export default {
  methods: {
    loading() {
      const loading = this.$loading({
        lock: true,
        text: "生成中……",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      setTimeout(() => {
        loading.close();
      }, 3000);
    },
    async downloadImg() {
      const loading = this.$loading({
        lock: true,
        text: "生成中……",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      try {
        let canvas = await html2canvas(this.$el, {
          // 转换为图片
          useCORS: true // 解决资源跨域问题
        });
        const dataURL = canvas.toDataURL("image/png");
        // this.showImg = truenp
        this.imgUrl = dataURL;
        loading.close();
        if (window.navigator.msSaveOrOpenBlob) {
          var bstr = atob(dataURL.split(",")[1]);
          var n = bstr.length;
          var u8arr = new Uint8Array(n);
          while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
          }
          var blob = new Blob([u8arr]);
          return window.navigator.msSaveOrOpenBlob(
            blob,
            "chart-download" + "." + "png"
          );
        }
        var a = document.createElement("a");
        a.download = "html2canvas";
        a.href = this.imgUrl;
        a.click();
      } catch (err) {
        loading.close();
        throw Error(err);
      }
    }
  }
};
</script>